$modalDepth : 1050;
$popupDepth: 10;
.popup {
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  transition: all .3s ease;
  &.photo-viewer {background-color:rgba(15, 15, 21, 0.8);}
  .tip-info {display:inline-block;width:18px;height:18px;}
}

.modal-dim{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .5);
}

.popup-dialog {
  @include abs-center($zIndex: $modalDepth);
  box-shadow: 0 0 10px 0 rgba(15, 15, 21, 0.05);
  background-color: #ffffff;

  .feed-info {
    margin-top:6px;
    li {
      float:left;
      color:#57576d;
      &:after {content:'';margin:0 6px;}
      &:last-child:after{display:none;}
    }

    &.period {
      position:relative;
      padding-left:28px;

      &:before {
        content:'';
        display:inline-block;
        width:18px;
        height:18px;
        background:url(../../assets/images/time-outline.svg) no-repeat center / 18px; opacity:0.7;
        @include pos-vertical($left:0);
      }
    }
  }
  .scroll-body {overflow-y:auto;}
}

.popup-header {
  position:relative;
  padding:30px 40px 20px;

  .popup-header-tit {font-size:18px;font-weight:500;}

  .popup-icon {
    width:50px;
    height:50px;

    &.error {background:url(../../assets/images/popup-icon-error.png) no-repeat;}
    &.search {background:url(../../assets/images/popup-icon-search.png) no-repeat;}
    &.edu {background:url(../../assets/images/popup-icon-edu.svg) no-repeat;}
    &.profile {background:url(../../assets/images/popup-icon-profile.svg) no-repeat;}
    &.link {background:url(../../assets/images/popup-icon-link.svg) no-repeat;}
    &.time {background:url(../../assets/images/popup-icon-time.svg) no-repeat;}
    &.vote {background:url(../../assets/images/popup-icon-vote.svg) no-repeat;}
    &.withdraw {background:url(../../assets/images/popup-icon-withdraw.svg) no-repeat;}
    &.change {background:url(../../assets/images/popup-icon-change.svg) no-repeat;}
    &.confirm {background:url(../../assets/images/popup-icon-confirm.svg) no-repeat;}
    &.add {background:url(../../assets/images/popup-icon-add.svg) no-repeat;}
    &.staff {background:url(../../assets/images/popup-icon-staff.svg) no-repeat;}
    &.leave {background:url(../../assets/images/popup-icon-leave.svg) no-repeat;}
    &.block {background:url(../../assets/images/popup-icon-block.svg) no-repeat;}
    &.success {background:url(../../assets/images/popup-icon-success.svg) no-repeat;}
  }

  .popup-close, .popup-btn {@include pos-abs($right:40px, $top:30px);}
}

.popup-type {
  padding-bottom:0;
  border-bottom:none;
  font-size:16px;
  color:#373737;
  &.sm {font-size:14px;}
}

.popup-cnt {
  padding:10px 40px;
  .write-tit {padding:18px 0 0 0;}
  .form-item {padding:18px 0;}
}

.popup-search {padding:10px 40px 20px;}

.popup-tit {
  font-size:18px;
  font-weight:500;
  + p, + div {padding-top:20px;}
}

.popup-sub-tit {padding:18px 0;font-size:16px;color:#373749;}

.popup-txt {
  letter-spacing:0;
  line-height:1.57;
  color:#57576d;
  &.type2 {color:#848499;margin-bottom:4px;}
}

.popup-input {padding:30px 0 10px 0;}

.popup-form-top {
  margin:0 40px;
  border-bottom:1px solid #f4f5f6;

  .form-item {
    padding:18px 0;
    border-top:1px solid #f4f5f6;
    &.selected, &.pd-desc {padding:10px 0;}
    &.post-classes {
      position:relative;
      p {
        color:#848499;
        padding-left:42px;

        .icon {border-radius: 50%;overflow: hidden;}
        .gr-name {font-size:12px; color:#57576d;}
        .cls-name {display:block;font-weight:500;color:#0f0f15;}
      }

      .icon {
        display:block;
        width:32px;
        height:32px;
        @include pos-vertical($left:0);
        &.icon-class {background:url(../../assets/images/profile-class.svg) no-repeat;}
      }

      .post-classes-select, .post-writer {@include pos-vertical($right:0);}

      .post-writer {
        padding-left:20px;
        &:before {
          content:'';
          width:14px;
          height:14px;
          margin-right:6px;
          vertical-align: middle;
          border-radius:50%;
          background:#ffba36 url(../../assets/images/member-manager.svg) no-repeat center / 8px;
          @include pos-vertical($left:0);
        }
        &.manager:before, &.crown:before {background:#ffba36 url(../../assets/images/member-manager.svg) no-repeat center / 8px;}
      }
    }

    .state-item {
      + .state-item {margin-top:6px;}

      p{
        position:relative;
        padding-left:28px;
        color:#57576d;
        &:before {
          content:'';
          display:inline-block;
          width:18px;
          height:18px;
          @include pos-vertical($left:0);
        }
        &.period:before {background:url(../../assets/images/time-outline.svg) no-repeat center / 18px; opacity:0.7;}
        &.repeat:before {background:url(../../assets/images/repeat.svg) no-repeat center / 18px;}
      }
    }
  }
}

.popup-form-cnt {
  .form-item {position:relative;margin:0 40px;padding:15px 0;border-bottom:1px solid #f4f5f6;}
  .post-cnt {padding:20px 0;border-bottom:0;}
  .form-textarea {height:100%;padding:0;border:none;}

  .form-box {
    @include pos-vertical($right:0);
    .form-group + .form-group {margin-left:30px;}
  }

  .form-create-list {
    border-top:1px solid #f4f5f6;

    .create-item-cnt {
      padding:0 40px;

      .create-sub-btn {
        @include pos-vertical($right:0);

        > a {
          display:block;
          float:left;
          margin-left:20px;
          &.add-img-btn {opacity:0.2;}
        }
      }

      // start:  
      .create-link-list {
        li {
          position:relative;
          padding:15px 0;
          border-bottom:1px solid #f4f5f6;

          .create-form {
            overflow: hidden;
            &.list-type{
              position:relative;
              padding:15px 0;
              border-bottom:1px solid #f4f5f6;
              background-color:#fff;
            }
            .num {
              width:20px;
              float:left;
              margin-right:10px;
              vertical-align: middle;
              color: rgba(55,55,73,0.2);
            }
            &.active.num {color:#57576d;}
            .form-group {float:left;}
          }
        }
      }
      // end:  

      // start:   
      .create-notice-list {
        li {
          position:relative;
          padding:15px 0;
          height: 52px;
          border-bottom:1px solid #f4f5f6;
        }
      }
      // end:   

    }

    .create-add-list {
      a {
        display:block;
        padding:13px 0;
        border-bottom:1px solid #f4f5f6;
        text-align:center;
        .btn-txt {vertical-align: middle;}
      }
    }
  }

  .add-curriculum {
    .tit {padding:18px 40px;font-size:16px;font-weight:500;}
    .add-curr-tit {margin-right:40px;float:left;font-weight:500;color:#848499;}
  }
}

.popup-form-btm {
  .form-add-list + .form-add-list {margin-top:20px;}
}

// start: enroll
.enroll-tit{margin: 0 40px;padding: 15px 0;font-weight: 500;color: #373749;}
.enroll-qna {
  margin: 0 40px;
  p {
    padding: 20px 0;
