.auto-resize{
  display: block;
  width: 100%;
  height:auto;
  min-height: auto;
  appearance: none;
}

.add-item{margin: 0 40px;}

//  
.add-item-top {
  position: relative;
  padding: 16px 0 14px;
  border-bottom: 1px solid #f4f5f6;
  .add-item-tit {margin-bottom: 0;font-weight: 500;}
  .add-item-btn {
    @include pos-vertical($right: 0);
    .txt-btn {
      font-weight: 500;
      + .txt-btn {margin-left: 20px;}
    }
  }
}

.add-item-cnt {
  position: relative;
  padding:10px 40px;

  // start: photo  
  .add-photo {
    position: relative;
    width: calc(100% - 80px);
    margin: 0 auto;
    overflow: hidden;
    height: 80px;
  }

  .add-photo-list {
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:80px;
    padding:10px 0;
    white-space:nowrap;
    transition: all .35s ease;
    li {
      overflow: hidden;
      position: relative;
      display: inline-block;
      width: 80px;
      height: 80px;
      margin-left:10px;
      &:first-child{margin-left:0;}
      .add-img-delete {@include pos-abs($top: 0, $right: 0);}
    }
  }

  .add-photo-control {
    position: absolute;
    width: 100%;
    height: 20px;
    top: 53%;
    transform: translateY(-50%);
    .btn {
      @include pos-vertical();
      &.scroll-prev {left: 0;}
      &.scroll-next {right: 0;}
      &.active {opacity: 1;}
    }
  }
  // end: photo  

  // start: vote
  .add-vote {
    padding:12px 0 12px 46px;
    border:1px solid #c9cad5;
    border-radius:5px;
    background:url(../../assets/images/vote.svg) no-repeat left 12px top 12px;
  }

  .add-vote-tit {font-size:16px;font-weight:500;line-height:1.5;}

  .add-vote-type {
    li {
      position:relative;
      float:left;
      padding-right:10px;
      margin-right:10px;
      font-size:12px;
      line-height:1.5;
      color:#848499;

      &:not(:last-child):after {
        content:'';
        display:block;
        width:1px;
        height:12px;
        background-color:#848499;
        @include pos-vertical($right:0)
      }
    }
  }
  // end: vote

  // start: link
  .add-link {
    position:relative;
    padding:20px;
    border:1px solid #c9cad5;
    border-radius:5px;
  }

  .add-link-cnt {
    width:calc(100% - 50px); //jbc
    vertical-align: top;
    .add-link-tit {font-weight:500;}
    .add-link-txt {margin:10px 0;font-size:12px;color:#57576d;}
    .add-link-url {
      a {
        display:block;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipsis;
        .link-logo {margin-right:10px;}
      }
    }
  }

  .add-link-thumb {
    width:160px;
    height:100%;
    overflow:hidden;
    @include pos-abs($top:0, $right:0);
  }
  // end: link

  // start: file
  .add-file-list {
    li {
      position:relative;
      p {
        padding:15px 0 15px 42px;
        border-bottom:1px solid #f4f5f6;
        background:url(../../assets/images/file.svg) no-repeat left center;
      }
      .add-file-delete {@include pos-vertical($right:0);}
    }
  }
  // end: file
}


