    @include pos-abs($left:50%, $top:50%);
    transform:translate(-50%, -50%);
    &.active{display:block;}
  }
}

/*viewer-control*/
.control {
  &.prev {@include pos-vertical($left:0);}
  &.next {@include pos-vertical($right:0);}
}

.viewer-page {
  @include pos-abs($left:50%, $bottom:18px);
  font-size:18px;
  font-weight:500;
  color:#fff;
  transform:translateX(-50%);
}

.modal-enter {opacity: 0;}
.modal-leave-active {opacity: 0;}

.modal-enter .popup-dialog,
.modal-leave-active .popup-dialog {
  transform: scale(1.1);
}

//draggable 
.ghost {opacity: 0.7;background: #c8ebfb;}
.flip-list-move {transition: transform 0.5s;}
.no-move {transition: transform 0s;}
