
.search-inner {
  width:1020px;
  margin:0 auto;
}

.search-top {
  box-shadow: 0 0 20px 0 rgba(15, 15, 21, 0.05);
  background-color:#fff;

  .search-bar {
    padding:10px 0 10px 42px;
    background:url(../../assets/images/search.svg) no-repeat left center;

    .form-search {
      width:100%;
      font-size:18px;
    }
  }
}

.search-word {
  background-color:#fff;

  .search-word-list {
    li {
      a {
        display:block;
        padding:13px 0 13px 42px;
        font-size:16px;
      }
    }
  }
}

.recommend-tit {
  width:1020px;
  margin:0 auto;
  padding:12px 0 10px 0;
  font-size:12px;
  font-weight:500;
}

.keyword {
  padding:25px;
  background-color:#fff;

  .keyword-list {
    li {
      float:left;
      margin-right:10px;

      a {
        display:block;
        padding:6px 14px;
        font-weight:500;
        border-radius:32px;
        color:#2d2d45;
        background-color:#f4f5f6;
      }
    }
  }
}

.search-classes {

  &.bg-white {background-color:#fff;}

  .group-tit {
    font-size:12px;
    font-weight:400;
    color:#50506a;
  }
  .class-tit {
    font-size:14px;
    font-weight:500;
  }

  p {
    font-size:12px;
    color:#2d2d45;
  }

  .class-admin, .class-tag {margin-top:6px;}

  .class-admin {
    position:relative;
    padding-left:20px;
    font-weight:400;

    &:before {
      content:'';
      position:absolute;
      display:block;
      top:0;
      left:0;
      width:14px;
      height:14px;
      background:url(../../assets/images/btn-round-manager.svg) no-repeat center / 100% 100%;
    }
  }

  .class-tag {
    a {
      position:relative;
      display:block;

      .search-more {
        position:absolute;
        top:0;
        right:0;
      }

      p {
        width:80%;
        font-weight:400;

        &.tag-none {
          opacity: 0.2;
        }
      }
    }
  }

}

.result-top {
  margin-top:30px;

  h2{
    padding:0 0 27px 20px;
    font-size:18px;
    font-weight:400;
    letter-spacing:0;
  }

  .result-filter {
    padding:20px 32px;
    border-radius:5px;
    background-color:#fff;

    input {
      + label {
        color:#50506a;
      }
      &:checked {
        + label {
          font-weight:500;
        }
      }
    }
  }

}

.result-cnt {
  position: relative;
  min-height:440px;
  background-color:#fff;
  .class-tag a .search-more {right:20px;}

  .tb-cnt {
    tr {
      th, td {
        &:first-child {padding-left: 30px;}
      }
    }
  }
}

.result-footer {padding:20px 0;background-color:#fff;}
