/* absolute  */
/* absolute vertical align center */
.wrapper {
    min-height: 100vh;
    background-color: #f4f5f6;
}

.wrapper .contents {
    min-height: calc(100vh - 238px);
}

header {
    background-color: #f4f5f6;
}

header .header-container {
    position: relative;
    width: 1020px;
    margin: 0 auto;
    padding: 24px 0;
}

header .header-container:after {
    content: "";
    display: block;
    clear: both;
}

header .header-container .class-select {
    position: absolute;
    top: 50%;
    left: 90px;
    transform: translateY(-50%);
}

header .header-container .class-select .form-select {
    min-width: 266px;
    padding: 10px 52px 10px 20px;
    border: 0;
    font-size: 14px;
    background-color: #f4f5f6;
}

header .header-container .logo {
    float: left;
    font-size: 0;
}

header .header-container nav {
    float: right;
}

header .header-container nav.pd-desc {
    padding-right: 206px;
}

header .header-container nav li {
    float: left;
    font-size: 14px;
    font-weight: 500;
    margin: 0 20px;
}

header .header-container nav li:last-child {
    margin-right: 0;
}

header .header-container .sub-menu {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    z-index: 1000;
}

header .header-container .sub-menu li {
    display: inline-block;
    margin: 0 6px;
}

header .header-container .sub-menu li:last-child {
    margin-right: 0;
}

header .header-container .sub-menu li > a {
    display: inline-block;
    width: 32px;
    height: 32px;
    vertical-align: middle;
}

header .header-container .sub-menu li .top-search {
    background: url(../../assets/images/search-white.svg) no-repeat;
}

header .header-container .sub-menu li .top-search.active {
    position: relative;
    background: url(../../assets/images/close-white.svg) no-repeat;
}

header .header-container .sub-menu li .top-search.active:after {
    content: '';
    display: block;
    width: 0px;
    height: 0px;
    position: absolute;
    left: 50%;
    bottom: -18px;
    transform: translateX(-50%);
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 7px solid #fff;
}

header .header-container .sub-menu li .top-alert {
    background: url(../../assets/images/icn-32-alert-white.svg) no-repeat;
}

header .header-container .sub-menu li .top-alert.active {
    position: relative;
}

header .header-container .sub-menu li .top-alert.active:after {
    content: '';
    display: block;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #ff4d4d;
    position: absolute;
    top: 2px;
    right: 2px;
}

header .header-container .sub-menu li .list-popup .sub-menu-btn {
    padding-right: 18px;
}

header .header-container .sub-menu li .list-popup .sub-menu-btn:after {
    content: '';
    display: block;
    width: 18px;
    height: 18px;
    background: url(../../assets/images/icn-18-arrow-down-white.svg) no-repeat center;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    transform: translateY(-50%) rotate(0deg);
    -webkit-transition: all 0.5s ease;
    transition: all 0.2s ease;
}

header .header-container .sub-menu li .list-popup .sub-menu-btn.active:after {
    transform: translateY(-50%) rotate(-180deg);
    -webkit-transition: all 0.5s ease;
    transition: all 0.2s ease;
}

header .header-container .sub-menu li .list-popup.black .sub-menu-btn:after {
    background-image: url(../../assets/images/icn-18-arrow-down.svg);
}

header .breadcrumbs {
    background-color: #fff;
}

header .breadcrumbs .breadcrumbs-inner {
    width: 1040px;
    margin: 0 auto;
    padding: 11px 0;
}

header .breadcrumbs .breadcrumbs-inner ul {
    overflow: hidden;
}

header .breadcrumbs .breadcrumbs-inner ul li {
    position: relative;
    float: left;
    font-size: 12px;
    color: #57576d;
}

header .breadcrumbs .breadcrumbs-inner ul li:after {
    content: '>';
    display: inline-block;
    padding: 0 3px;
}

header .breadcrumbs .breadcrumbs-inner ul li:last-child:after {
    display: none;
}

header.dark {
    background-color: #1d1d4c;
}

header.dark .menu {
    color: #fff;
}

header.white {
    background-color: #fff;
}

header.white .menu {
    color: #45454a;
}

header.white .sub-menu li .top-search {
    background: url(../../assets/images/search.svg) no-repeat;
}

header.white .sub-menu li .top-search.active {
    background: url(../../assets/images/close.svg) no-repeat center;
}

header.white .sub-menu li .top-alert {
    background: url(../../assets/images/alert.svg) no-repeat;
}

footer {
    background-color: #f4f5f6;
    border-top: 1px solid #c9cad5;
}

footer .footer-inner {
    width: 1020px;
    height: 180px;
    padding-top: 30px;
    margin: 0 auto;
}

footer .footer-logo {
    float: left;
    width: 77px;
    height: 14px;
    margin-right: 30px;
    background: url(../../assets/images/logo-inition.png) no-repeat;
}

footer ul li {
    float: left;
    margin-left: 30px;
    color: #57576d;
}

footer ul.footer-address {
    float: left;
}

footer ul.footer-address li {
    font-size: 12px;
}

footer ul.footer-menu {
    float: right;
}

footer ul.footer-menu li {
    font-size: 16px;
}

footer.dark {
    background-color: #1d1d4c;
    border-top: 1px solid #57576d;
}

footer.dark .footer-logo {
    background: url(../../assets/images/logo-inition-white.png) no-repeat;
}

footer.dark ul li {
    color: #9d9db2;
}

.contents-inner {
    position: relative;
    width: 1020px;
    margin: 0 auto 30px auto;
}

.contents-inner .contents-group {
    margin-top: 20px;
    padding: 0 50px 150px;
}

.contents-inner .contents-group .contents-left {
    width: 280px;
    float: left;
}

.contents-inner .contents-group .contents-right {
    width: 600px;
    float: right;
}

.contents-inner .contents-group .contents-right .cnt-top {
    position: relative;
}

.contents-inner .contents-group .contents-right .cnt-tit {
    font-size: 18px;
    font-weight: 500;
}

.contents.mypage .contents-left {
    width: 300px;
}

label, .label {
    display: block;
    font-weight: 500;
    color: #57576d;
}

input::-moz-placeholder,
textarea::-moz-placeholder {
    color: rgba(55, 55, 73, 0.2);
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
    color: #d0d0d0;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
    color: #d0d0d0;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: 12px;
    color: #57576d;
    background: #fff url(../../assets/images/arrow-down.svg) no-repeat center right;
    background-size: 18px;
}

select::-ms-expand {
    display: none;
}

.form-control {
    width: 100%;
    padding: 0 0 10px 0;
    line-height: 20px;
    font-size: 14px;
    border: none;
    border-bottom: 1px solid #c9cad5;
    border-radius: 0;
    box-shadow: none;
    background-color: transparent;
}

.form-control[disabled], .form-control[readonly] {
    color: #848499;
}

.form-control:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 1px;
    background-color: #0f0f15;
}

.form-control:focus {
    box-shadow: none;
}

.form-control:focus:before {
    width: 100%;
}

.form-control:focus + .form-message.count {
    color: #57576d;
}

.form-control:focus + .form-message.time {
    display: block;
}

.form-control:focus + .form-message.pw-show {
    opacity: 1;
}

.form-control[type="number"] {
    width: 40px;
    padding: 4px 2px;
    border: none;
    text-align: right;
    background-color: #fff;
}

.form-control[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.form-control[type="number"]:focus {
    background-color: #f4f5f6;
}

.form-control.error {
    border-color: #ff4d4d;
}

.form-control.approval {
    border-color: #629f00;
}

.form-message {
    position: absolute;
    left: 0;
    margin-top: 4px;
    font-size: 12px;
    color: #57576d;
    /* form-control focus  */
    /* form-control password focus  */
}

.form-message.count {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    color: #848499;
}

.form-message.error {
    color: #ff4d4d;
}

.form-message.approval {
    color: #629f00;
}

.form-message.ms-time {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    display: none;
    font-size: 14px;
    color: #ff4d4d;
}

.form-message.pw-show {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 24px;
    height: 24px;
    opacity: 0.2;
    background: url(../../assets/images/eye.svg) no-repeat;
}

.form-message.pw-show.active {
    background: url(../../assets/images/eye-off.svg) no-repeat;
}

.form-switcher {
    position: relative;
    display: block;
}

.form-switcher input {
    visibility: hidden;
    position: absolute;
}

.form-switcher input:checked + label {
    background-color: #adc500;
}

.form-switcher input:checked + label:after {
    transform: translateX(100%);
}

.form-switcher label {
    background-color: #f4f5f6;
    border-radius: 20px;
    display: block;
    position: relative;
    transition: all 0.3s ease;
    width: 42px;
    height: 24px;
}

.form-switcher label:after {
    z-index: 3;
    content: '';
    position: absolute;
    top: 3px;
    left: 3px;
    width: 18px;
    height: 18px;
    background-color: #fff;
    border-radius: 50%;
    transition: all 0.3s ease;
    transform: translateX(0);
}

.form-textarea {
    width: 100%;
    min-height: 80px;
    padding: 10px 14px;
    border: 1px solid #c9cad5;
    resize: none;
    line-height: 22px;
    font-size: 14px;
    font-family: "Noto Sans KR", sans-serif;
    color: #373749;
    background-color: #fff;
}

.form-textarea.h-full {
    height: 100%;
}

.form-textarea + .form-message.count {
    position: relative;
    float: right;
    transform: none;
}

.form-select {
    position: relative;
    padding: 7px 40px 7px 17px;
    border: 1px solid #f4f5f6;
