.panel-group {
    margin-bottom: 0;
}

.panel {
    margin-top: 8px;
    margin-bottom: 0;
    box-shadow: none;
    border-bottom: 1px solid #f4f5f6;
}

.panel:first-child {
    margin-top: 0;
}

.panel-heading {
    padding: 15px 20px;
    background-color: #fff;
}

.panel-heading .bt-cnt {
    position: relative;
    display: block;
    width: 100%;
    line-height: 18px;
}

.panel-heading .bt-cnt .bt-icon {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%) rotate(-180deg);
    transition: all .3s ease-in-out;
}

.panel-heading .bt-cnt.collapsed .bt-icon {
    transform: translateY(-50%) rotate(0deg);
    transition: all .3s ease-in-out;
}

