.home-main {
  padding-top: 80px;

  .main-txt {
    padding-left: 0;
  }
}

.notice {
  position: absolute;
  width: 100%;
  top: 10px;
  left: 0;

  .notice-cnt {
    display: inline-block;
    padding: 6px 14px;
    font-size: 12px;
    color: #57576d;
    background-color: #fff;
    border-radius: 15px;

    .notice-tit {
      vertical-align: middle;

      strong {
        color: #0f0f15;
      }

      span {
        display: inline-block;
        margin: 0 6px;
      }
    }
  }
}

.home-classes {
  position: relative;
  margin-top: -110px;
  margin-bottom: 432px;

  .list-align {
    @include pos-abs($right: 0, $top: 0);

    .form-select {
      padding: 10px 40px 10px 21px;
      border: 0;
      font-size: 14px;
      font-weight: 500;
      line-height: 1.45;
      background-color: #fff;
    }
  }

  .classes-card {
    margin-top: 20px;

    .classes {
      display: flex;
      flex-flow: row wrap;
      justify-content: flex-start;

      .item {
        position: relative;
        width: 240px;
        height: 306px;
        padding: 40px 30px 20px 30px;
        margin-right: 20px;
        margin-bottom: 20px;
        border-radius: 5px;
        box-shadow: 0 0 10px 0 rgba(15, 15, 21, 0.05);
        background-color: #ffffff;

        &:nth-child(4n) {margin-right: 0;}

        .class-info {
          display: block;
          .info-img {
            width: 80px;
            height: 80px;
            margin: 0 auto;
            text-align: center;
            span {
              display: inline-block;
              background-color: red;
              border-radius: 50%;
              overflow: hidden;
            }
            &.crown {
              position: relative;
              &:before {
                content: '';
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                @include pos-abs($top: 2px, $right: 2px);
                background: #ffd100 url('../../assets/images/member-manager.svg') no-repeat center / 10px;
              }
            }
          }

          .info-tit {
            margin-top: 20px;
            text-align: center;
            p {font-size: 16px;color: #0f0f15;font-weight: 500;}
            span {font-size: 14px;color: #848499;}
          }
        }

        .info-sub {
          margin-top: 12px;
          text-align: center;
          font-size: 12px;
          color: #848499;
          span {
            line-height: 1.5;
            &:nth-child(2) {
              position: relative;
              padding: 0 14px;
              &:before, &:after {
                content: '';
                position: absolute;
                display: inline-block;
                width: 2px;
                height: 2px;
                top: 50%;
                transform: translateY(-50%);
                background-color: #57576d;
                vertical-align: middle;
              }

              &:before {left: 6px;}
              &:after {right: 6px;}
            }
          }
        }

        .class-update {
          position: absolute;
          width: 100%;
          left: 0;
          bottom: 20px;
          padding: 0 20px 0 30px;

          .update {
            position: relative;
            float: left;
            padding: 7px 0;
            color: #c9cad5;
            font-size: 12px;

            &.active {
              padding-left: 10px;
              color: #57576d;

              &:before {
                content: '';
                position: absolute;
                display: block;
                width: 4px;
                height: 4px;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                border-radius: 50%;
                background-color: #ff4d4d;
              }
            }
          }

          button {float: right;}
        }

        &.new-classes {
          padding: 84px 30px;
          text-align: center;
          p {
            padding-top: 90px;
            font-size: 16px;
            color: #c9cad5;
            background: url(../../assets/images/class.png) no-repeat center top;
          }
        }
      }
    }
  }
}

.class-photo {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin-right: 10px;
  border-radius: 50%;
}

.class-notify {
  margin-bottom:20px;
  border-radius: 5px;
  box-shadow: 0 0 10px 0 rgba(15, 15, 21, 0.05);
  background-color:#fff;

  .notify-top {padding:20px 0 20px 30px;}
  .notify-cnt {
    height:280px;
    overflow-y:auto;
    .notify-list {
      li {
        a {
          display:block;
          padding: 14px 30px;
          .class-name {color: #57576d;}
          &.active {
            background-color: #f4f5f6;
            .class-name {font-weight: 500;color: #0f0f15;}
          }
        }
      }
    }
  }
}

.class-calendar {
  border-radius: 5px;
  box-shadow: 0 0 10px 0 rgba(15, 15, 21, 0.05);
  background-color: #fff;
}

.classes-todo {
  border-radius: 5px;
  box-shadow: 0 0 10px 0 rgba(15, 15, 21, 0.05);
  background-color: #fff;

  .schedule-top {
    padding: 20px 0 10px 30px;
  }

  .schedule-cnt {
    .schedule-list {
      li {
        position:relative;
        padding:10px 30px 10px 80px;

        + li {margin-top:10px;}

        .date {
          position:absolute;
          top:10px;
          left:30px;
          font-weight:500;
          color:#57576d;
        }

        .detail-list {
          .item {
            position:relative;
            padding-left:14px;

            .bullet {
              position:absolute;
              display:inline-block;
              width:6px;
              height:6px;
              top:6px;
              left:0;
              border-radius:50%;

              &.orange {background-color:#f9862a;}
              &.green {background-color:#629f00;}
              &.ps-blue {background-color:#6d8fdf;}
              &.red {background-color:#ff4d4d;}
              &.sky-blue {background-color:#1cdbc9;}
              &.blue {background-color:#2183de;}
            }
            .schedule-tit {font-weight:500;}
            .time {font-size:11px;line-height:1.69;color:#57576d;}
            + .item {margin-top:20px;}
          }
        }
      }
    }
  }
}

.class-home {
  padding-bottom:10px;
  border-radius: 5px;
  box-shadow: 0 0 10px 0 rgba(15, 15, 21, 0.05);
  background-color: #ffffff;
  .menu-list{padding-left:0;}
  .class-home-info {
    padding:40px 20px 28px;
    text-align:center;
    .info-img {
      width:80px;
      height:80px; //jbc
      overflow:hidden;
      margin:0 auto;
      .circle{width:80px;height:80px;border-radius: 50%;/*jbc*/}
      .img-change {position:absolute;bottom:0;right:0;width:32px;}
    }
    .info-txt {
      margin-top:20px;
      .info-cn {font-size:16px;font-weight:500;}
      .info-or {color:#848499;}
      .info-list {
        overflow:hidden;
        margin:10px auto;
        li {
          display:inline-block;
          padding:8px 0;
          font-size:12px;
          color:#848499;
          &:after {content:'';display:inline-block;margin:0 2px 0 5px;}
          &:last-child:after {display:none;}
        }
      }
      .info-tag {font-size:12px;color:#57576d;}
    }
    .info-img~.info-txt{display:block;margin-top:20px;}
    .class-bookmark {margin-top:20px;}
  }

  .class-menu {
    .menu-list {
      li {
        a {
          display:block;
          padding:15px 0 15px 74px;
          color:#57576d;
          background-repeat: no-repeat;
          background-position: 34px 12px;

          &.type1 {background-image:url(../../assets/images/classmenu-home-outline.svg);}
          &.type2 {background-image:url(../../assets/images/classmenu-notice-outline.svg);}
          &.type3 {background-image:url(../../assets/images/classmenu-plan-outline.svg);}
          &.type4 {background-image:url(../../assets/images/classmenu-file-box-outline.svg);}
          &.type5 {background-image:url(../../assets/images/classmenu-curriculum-outline.svg);}
          &.type6 {background-image:url(../../assets/images/classmenu-setting-outline.svg);}

          &.active, &:hover {
            color:#0f0f15;
            background-color:#f4f5f6;

            &.type1 {background-image:url(../../assets/images/classmenu-home.svg);}
            &.type2 {background-image:url(../../assets/images/classmenu-notice.svg);}
            &.type3 {background-image:url(../../assets/images/classmenu-plan.svg);}
            &.type4 {background-image:url(../../assets/images/classmenu-file-box.svg);}
            &.type5 {background-image:url(../../assets/images/classmenu-curriculum.svg);}
            &.type6 {background-image:url(../../assets/images/classmenu-setting.svg);}
          }
        }
      }
    }
  }
}

.info-img {
