.fd-head {position: relative;
  .fd-tit {font-size: 18px;}
  .fd-writer {
    padding: 21px 0;
    .class-name {font-weight: 500;color: #0f0f15;}
    .writer {
      font-size: 12px;
      span {
        &.name {font-weight: 400;}
        &.time {
          position: relative;
          margin-left: 3px;
          color: #57576d;
        }
        &.noti {color: #d42b2b;}
        &:after {
          content: '';
          display: inline-block;
          margin-left: 6px;
        }
        &:last-child:after {display: none;}
