    }

    &.type2 {
      padding: 10px 0 20px;
      .writer {font-size: 14px;
        .name {
          position: relative;
          padding-left: 22px;

          &:before {
            content: '';
            position: absolute;
            display: block;
            width: 14px;
            height: 14px;
            top: 0;
            left: 0;
            border-radius: 50%;
            background: #57576d url(../../assets/images/member-staff.svg) no-repeat center / 8px 8px;
          }

          &.manager {
            &:before {background:#ffba36 url(../../assets/images/member-manager.svg) no-repeat center / 8px 8px;}
          }
        }
