}

.fd-inner {
  position: relative;
  margin-top: 20px;

  .fd-inner-txt {
    position: relative;
    padding: 8px 0;
    font-weight: 500;
    color: #57576d;
  }

  .vote-count {
    @include pos-vertical($right: 0);
    font-size: 12px;
    color: #57576d;
  }
}

.fd-foot {
  margin-top:10px;
  padding:20px 0;
  .fd-viewer {color:#c9cad5;}
  .fd-comment {float:right;}
}

.classes-feed {
  margin-top: 10px;

  &.home {
    .feed-item {
      padding: 32px 40px 30px 76px;
      background-repeat: no-repeat;
      background-position: 30px 30px;

      &.cal {background-image: url(../../assets/images/btn-round-cal.svg)}
      &.edu {background-image: url(../../assets/images/btn-round-edu.svg)}
      &.noti {background-image: url(../../assets/images/btn-round-noti.svg)}
    }
    .fd-foot {margin-top:20px;padding:0;}
    .notify-feed {
      .fd-writer {
        padding: 2px 0 0 0;
        .writer {font-size: 12px;}
      }
    }
  }

  &.notification {
    .accordion-btn {
      color: #848499;
      &.active {color: #f9862a;}
    }
    .notify-feed {
      .fd-tit {color: #000;opacity: 0.3;font-weight: 500;}
      .fd-schedule {font-size: 12px;color: #f9862a;}
    }

    .img-list {
      li {
        &.img-item:nth-child(1),
        &.img-item:nth-child(2),
        &.img-item:nth-child(3){
