.no-notify, .simple-notify {
  position:relative;
  height:600px;
  border-radius: 5px;
  background-color:#fff;

  .no-data, .simple-data{
    @include pos-abs($top:50%, $left:50%);
    transform:translate(-50%, -50%);
    text-align:center;

    p {margin-top:20px; font-size:16px; color:#848499;}
    span {margin-top:10px; color:#c9cad5;}
  }
}


.img-list {
  overflow: hidden;
  height: 320px;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  li {
    position:relative;
    img {width:100%; object-fit: fill}
    &.img-item:nth-child(1){
      grid-row-start: 1;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 3;
      a{
        img {
          width: 238px;
          height: 320px;
        }
      }
    }
    &.img-item:nth-child(2){
      margin-bottom: 10px;
      a{
        img {
          width: 236px;
          height: 150px;
        }
      }
    }

    &.img-item:nth-child(3){
      a{
        img {
          width: 236px;
          height: 160px;
        }
        &:after{
          content:attr(data-count);
          position:absolute;
          display:block;
          top:50%;
          left:50%;
          transform:translate(-50%, -50%);
          color:#fff;
          font-size:24px;
          text-align:center;
        }
        &:before {
          content:'';
          position:absolute;
          display:block;
          width:100%;
          height:100%;
          top:0;
          left:0;
          color:#fff;
          font-size:24px;
          text-align:center;
          background-color:rgba(0,0,0,0.5);
        }
      }
    }
  }
  .img-item:nth-child(3) .pseudo-del:before{content: none;}
}


/*   1    */
.img-list li:first-child:nth-last-child(1){
  grid-row-start: auto;
  grid-row-end: auto;
  grid-column-start: auto;
  grid-column-end: 3;
}
/*   2    320px  */
.img-list li:first-child:nth-last-child(2) ~ li a img {height:320px;}


.file-list {
  li {
    float: left;
    margin: 0 10px 10px 0;
    a {
      display: block;
      position: relative;
      padding: 14px 20px 14px 46px;
      border: 1px solid #c9cad5;
      border-radius: 5px;
      background-color: #fff;
      &:before {
        content: '';
        position: absolute;
        display: block;
        width: 24px;
        height: 24px;
        top: 50%;
        left: 12px;
        transform: translateY(-50%);
        background: url(../../assets/images/pic7.png) no-repeat;
      }
    }
  }
}

.schedule-list {
  padding: 14px 20px 14px 48px;
  border-radius: 5px;
  border: solid 1px #c9cad5;
  background: url(../../assets/images/classmenu-plan-outline.svg) no-repeat left 23px top 14px / 18px 18px;
  li {color: #57576d;}
}

.vote-area {
  position: relative;
  margin-top: 10px;
  padding: 12px 0 12px 46px;
  border: 1px solid #c9cad5;
  border-radius: 5px;
  &:before {
    content: '';
    position: absolute;
    display: block;
    width: 24px;
    height: 24px;
