    height: 400px;
    .search-none-inner {
      position: absolute;
      width: 100%;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      p {
        text-align: center;
        &.m-txt {padding: 20px 0 10px 0;font-size: 16px;color: #848499;}
        &.s-txt {color: #c9cad5;}
      }
    }
  }

  .setting-list {
    margin: 10px 0 20px;
    background-color: #fff;
    .list-item {
      position: relative;
      padding: 15px 20px;
      border-bottom: 1px solid #f4f5f6;
      .list-tit {font-size: 14px;color: #373749;}
      .setting-btn {position: absolute;top: 50%;right: 20px;transform: translateY(-50%);}
      &.sub-list-item {
        position: relative;
        border-bottom: none;
        .list-tit {color: #848499;}
      }
      &.disabled {opacity: 0.2;}
    }
  }
  .scroll-body {
    overflow-y: scroll;
    &.file {
      padding-right: 200px;
      margin-right: -200px;
      -ms-overflow-style: none; /* IE and Edge */
      &::-webkit-scrollbar {display: none; /* Chrome, Safari, Opera*/}
    }
  }
}
