.class-created {
  min-height: 305px;
  padding: 30px 40px 0 40px;

  .class-type {
    margin:30px 0;
    .type-list {
      li {
        float:left;
        width:190px;
        margin-right:15px;
        a {
          display:block;
          padding:98px 0 18px 0;
          border:1px solid #c9cad5;
          border-radius:5px;
          text-align:center;
          font-size:16px;
          color:#848499;
        }
        &.active a, &:hover a{border-color:#373749;color:#0f0f15;}
        &.type1{background:url(../../assets/images/school.png) 50% 18px no-repeat;}
        &.type1.active, &.type1:hover{background:url(../../assets/images/school-on.png) 50% 18px no-repeat;}
        &.type2 {background:url(../../assets/images/institute.png) 50% 18px no-repeat;}
        &.type2.active, &.type2:hover {background:url(../../assets/images/institute-on.png) 50% 18px no-repeat;}
        &.type3{background:url(../../assets/images/group.png) 50% 18px no-repeat;}
        &.type3.active, &.type3:hover {background:url(../../assets/images/group-on.png) 50% 18px no-repeat;}
      }
    }
  }
  .info-icon {display: inline-block;width: 18px;height: 18px;margin-left: 2px;}
}

.classes-add-btn {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  right: 0;
  border-radius: 50%;
  background: #1d1d4c url(../../assets/images/icn-80-add-btn.svg) no-repeat;
  background-size: contain;
}

.profile-list {
  .list-item {
    padding:15px 40px;
    border-bottom:1px solid #f4f5f6;
    .list-tit {color:#848499;}
    .basic-info {
      text-align:right;
      p {margin-bottom: 4px;}
      .basic-info-item {
        display:none;
        &.show {display:inline-block;}
      }
      .edit-btn {margin-left:10px;}
      .list-popup {
        text-align:left;
        .list-popup-btn span {margin-right:0;font-size:12px;color:#57576d;}
      }
    }
  }
}

.class-basic-info {
  .info-top {
    margin-top:10px;
    text-align:center;
    .group-type {font-weight:700;}
  }
  .info-form {
    padding:20px 40px 12px;
    label, .label {margin-bottom:10px;}
    .label {display:inline-block;}
  }
}


.add-curriculum {
  .tit {padding:18px 40px;font-size:16px;font-weight:500;}
  .add-curr-item {
    position:relative;
    padding:15px 40px;
    border-bottom:1px solid #f4f5f6;
    .add-curr-tit {
      float:left;
      font-weight:500;
      color:#848499;
      .curr-num {display:inline-block;min-width:70px;}
    }
    .form-box {@include pos-vertical($right:40px)}
  }
}

.form-txt, .date-area.ft-desc{color: #848499;}

.search-result {
  position: relative;
  height: 320px;
  .search-result-cnt {
    height:100%;
    overflow-y:auto;
    border-top:1px solid #f4f5f6;
    border-bottom:1px solid #f4f5f6;
    .search-result-item {
      li {
        display:table;
        width:100%;
        border-bottom:1px solid #f4f5f6;
        .result-link {
          display:table;
          table-layout: fixed;
          width:100%;
          &:hover {background-color:#f4f5f6;}
        }
        .tb-cell {
          &.result-tit {width:38%;padding:15px 0 15px 40px;}
          &.result-sub {
            padding:15px 0 15px 20px;
            color:#848499;
          }
        }
        &:last-child {border-bottom:0;}
      }
    }
  }
  .search-none {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 16px;
    letter-spacing: 0;
    color: #848499;
  }
}


/*   router-view   - ksj */
.non-popup {
  background: #fff;
  .scroll-body {overflow-y:auto;}
}

// pagination
.pagination {
  width:100%;
  padding:14px 0;
  text-align:center;
  ul {
    display:inline-block;
    overflow: hidden;
    li {
      float:left;
      a {
        display:block;
        padding:2px 0;
        width:32px;
        height:32px;
        border-radius:50%;
        line-height:32px;
        text-align:center;
      }
      &.num {
        &.active , &:hover{
          a{background-color:#ff4d4d;color:#fff;}
        }
      }
    }
  }
  .arrow {
    display: inline-block;
    margin:0 10px;
    opacity:0.2;
    vertical-align: top;
    line-height: 32px;
    &.active {opacity: 1;}
  }
}
